export default {
    en2kh(en) {
        const khm = '០១២៣៤៥៦៧៨៩';
        const eng = '0123456789';
        var kh = en.toString();
        for (let i = 0; i < eng.length; i++) {
            kh = kh.replaceAll(eng[i], khm[i]);
        }
        kh = kh.replaceAll('.', ',');
        return kh;
    },
}