import consts from "./consts";

export default {
    getExpiryStatus(feature){
        const expiry = feature.properties.lic_expiry;
        if(!expiry) { return consts.expiry.NA; }
        if(new Date(expiry) < new Date()) { return consts.expiry.expired; }
        return consts.expiry.active;
    },
    
    getYear(feature) {
        const yearNotFound = consts.labelOtherYears;
        const date = this.getDate(feature);
        if(!date) return yearNotFound;
        const year = date.getFullYear();
        return isNaN(year)? yearNotFound : year;
    },
    getDate(feature){
        const licDate = feature.properties.lic_date;
        if(!licDate) return null;
        return new Date(licDate);
    },
    getType(feature){ return feature.properties.lic_type; },
    getCategory(feature){ return feature.properties.category; },
    getCode(feature) { return feature.properties.pro_code; },
    isSandProject(feature) { 
        if(feature.category){
            return feature.category === consts.sandCategory;
        }
        return this.getCategory(feature) === consts.sandCategory; 
    },

    getLabelAndData(map){
        const labels = Object.keys(map);
        const data = Object.values(map);
        return {labels, data};
    },
}