const t = require('@turf/turf');

export default {
    
    // GEOJSON
    geojson: {
        empty(){ return {features:[]}; },
        create(features, name='features') { return {
            name,
            features,
            type: 'FeatureCollection',
            crs: { 
                type: 'name',
                name: 'urn:ogc:def:crs:OGC:1.3:CRS84',
            },
        }},

        isEmpty(geojson){
            return geojson.features.length === 0;
        },

        filter(geojson, filters){
            const features = geojson.features.filter(f => filters(f));
            return {...geojson, features: features};
        },

        filterByProperty(geojson, propertyName, value, filters){
            if(!filters) {return this.filter(geojson, (f) => {return f.properties[propertyName] === value});}
            return this.filter(geojson, (f) => {return f.properties[propertyName] === value && filters(f)});
        },

        setStyleType(geojson, type){
            const features = geojson.features.filter(f => {
                f.properties.stype = type;
                return f;
            });
            return {...geojson, features};
        },

        features2DataTable(features){
            return features.map(f => {
                                const properties = f.properties;
                                properties.feature = f;
                                return properties
                            });
        },

        dataTable2Features(data){
            return data.map(d => d.feature);
        },
        dataTable2GeoJson(data){
            return this.create(this.dataTable2Features(data));
        }
    },

}