<template>
    <div class="panel" :style="size" >
        <div v-if="minimized">
            <div  class="title-text ">{{title}}</div>
            <div  class="inline-content">
                <div class="fa fa-times close-button " @click="$emit('close')"></div>    
                <div class="far fa-square close-button " @click="minimized=false"></div>    
            </div>
        </div>

        <div class="inline-content">
            <div v-if="!minimized" class="fa fa-times close-button " @click="$emit('close')"></div>    
            <div v-if="!minimized" class="fa fa-minus close-button " @click="minimized=true"></div>
        </div>
        
        <slot v-if="!minimized" />
    </div>
    
</template>


<script>
export default {
    name: 'FloatingPanel',
    props:{
        title: {
            type: String,
            default: 'MoE/EIA Projects'
        },
        width: {
            type: Number,
            default: 100
        },
        height: {
            type: Number,
            default: 50
        }
    },

    data(){
        return {
            minimized: false
        }
    },

    computed:{
        size(){
            const w = this.minimized? 220 : this.width;
            const h = this.minimized? 33 : this.height;
            return `width: ${w}px; height: ${h}px; `;
        }
    }
}
</script>


<style scoped>

.inline-content {
    float: right;;
    overflow: hidden;
    transform: translateY(2px);  
}

.title-text {
    margin: 5px;
    float: left;
}

.panel {
    background-color: aliceblue;
    padding: 1px 0px 0 0;
}

.panel{
  transform: translateY(80px);
}

.close-button {
    color: gray;
    float: right;
    padding: 3px 5px;
    
    margin: 1px 3px 0px 0px;
    border: 1px transparent solid;
}
.close-button:hover {
    cursor: pointer;

    border: 1px gray solid;
}
</style>